import { onValue, ref } from 'firebase/database';
import React, { useEffect, useRef, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom';
import { db, writeToDataBaseSendMessage } from '../firebase';
import { useContextmenu } from '../hooks/use-Contextmenu';
import { getLocalStorage } from '../utils/localstorage';
function SendUserList({setOpen}) {
    const show = useRef(false)
    const [users,setUsers] = useState([])
    const navPath = useLocation()
    const nav = useNavigate()
    const chatId = navPath.pathname.replaceAll('chats', '').replaceAll('/', '')
    const myId = getLocalStorage('store').id
    const youId = chatId.replaceAll(myId, "")
    const messag = useContextmenu().messeg
    setTimeout(() => show.current = true,0)
    useEffect( () =>{
         const onSub = onValue(ref(db,`usersChat/${myId}`), snapshot =>{
          const users = snapshot.val()
          if (users) {
            setUsers(users ?  Object.keys(users).map(key =>({
              ...users[key],
              id: key
          })): [])
          } 
        })
        return () => onSub()
      },[])
   const hiddenClick = (e) =>{
    if(e.target.dataset.hidden){
     setOpen(true)
    }
   }
   const hidden = () =>{
     setOpen(true)
   }
   function linkToChat(id1,id2) {
    let chatId = ''
    if (id1 > id2) { 
    chatId = id1 + id2
    return chatId
    }else{
        chatId = id2 + id1
        return chatId
    }
    }
   const writeToDataBase = (myId,userId) =>{
        let chatId = linkToChat(myId,userId)
        writeToDataBaseSendMessage({
            chatId,
            myId,
            userId,
            messag
        })
        nav(`/chats/${chatId}`)
        hidden()
    }
  return (
    <div onClick={hiddenClick} data-hidden='overlay' className={`sendModal ${show.current ? 'showModal' : 'hiddenModal'}`}>
        <div className='container-usersSendModal'>
        <button data-hidden='overlay' className='btn btn-sendModal'></button>
        <p className='textSendModal'>   </p>
            <ul className='usersSendModalList'>
                {
                    users.length > 0 && users.map( (users) => myId !== users.id && youId !== users.id && <div onClick={() => writeToDataBase(myId,users.id)}
                    className='chat-wrap chat-wrap-search'
                     key={users.token}>                        
                  <li  className='chats__item'  >
              {users.img ?
              <>
                                  <img className='chats__img' src={users.img} alt="" />
              </>
            :
                ''
            }
            <div className='chats__name-wrap'>
            <p className='chats__name'>
                {users.name}
              </p>
            </div>
            </li>   
          </div> 
          )
                }
            </ul>
        </div>
    </div>
  )
}

export default SendUserList