import React, { useRef, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useContextmenu } from '../hooks/use-Contextmenu'
import { setOpneContextmenu } from '../store/slices/contextmenu'
import { ReactionBarSelector } from '@charkour/react-reactions';
import { db, setReactions } from '../firebase';
import SendUserList from './SendUserList';
import { ref, remove } from 'firebase/database';
import { useLocation } from 'react-router-dom';
import { getLocalStorage } from '../utils/localstorage';

function Contextmenu() {
  const [sendModal,setSendModal] = useState(true)
  const dispatch = useDispatch()
  const navPath = useLocation()
  const optionsContextmenu = useContextmenu()
  const chatId = navPath.pathname.replaceAll('chats', '').replaceAll('/', '')
  const myId = getLocalStorage('store').id
  const userId = chatId.replaceAll(myId, "")
  const hiddenContextmenuMouse = (e) =>{
    e.target.dataset.hidden && dispatch(setOpneContextmenu({
      open: false
    }))
  }
  const hiddenContextmenu = () =>{
    dispatch(setOpneContextmenu({
      open: false
    }))
  }
  const saveReactions = (item) =>{
      setReactions({
        chatId: optionsContextmenu.chatId,
        reactionsItem: item,
        messageId: optionsContextmenu.messageId,
      })
  }
  const copyMessage = () =>{
    window.navigator.clipboard.writeText(optionsContextmenu.text)
    hiddenContextmenu()
  }
  const send = () =>{
    setSendModal(false)
  }
  const deleteMessage = () =>{
    remove(ref(db,`chat/${chatId}/messages/${optionsContextmenu.messageId}`))
    hiddenContextmenu()
  }
  return (
    sendModal ? 
    optionsContextmenu.open && <div onMouseOver={hiddenContextmenuMouse} data-hidden='overlay' className="contextmenu-wrap" style={{
      position: 'fixed',
      top: '0px',
      left: '0px',
      right: '0px',
      bottom: '0px',
      width: '100%',
      height: '100%',
      overflow: 'hidden',
      zIndex: '1000',
      padding: '30px',
    }}>
                <div  style={{
                  marginLeft: `${optionsContextmenu.x - 170 > 0 ? `calc(${optionsContextmenu.x}px - 170px)` : '0px'}`,
                  marginTop: `${optionsContextmenu.y - 130 > 0 ? `calc(${optionsContextmenu.y}px - 170px)` : '0px'}`,
                  zIndex: '2000',
                  display: 'flex',
                }}  className='contextmenu'>
                    <ReactionBarSelector onSelect={saveReactions} 
                    reactions={[
                      {
                        node: <div></div>, key: "",
                      },
                      {
                        node: <div></div>, key: "",
                      },
                      {
                        node: <div></div>, key: "",
                      },
                      {
                        node: <div></div>, key: "",
                      },
                      {
                        node: <div></div>, key: "",
                      }
                    ]}
                    iconSize={20} style={{
                      border: 'none',
                      fontSize: '0px',
                      display: 'grid',
                      background: 'rgb(28,21,28)',
                      background: 'linear-gradient(90deg, rgba(28,21,28,1) 0%, rgba(45,25,35,0.98) 50%, rgba(28,21,28,1) 100%)',
                      paddingRight: '7px',
                      height: '100%',
                      padding: '0px',
                      width: '30px',
                      justifyContent: 'center'                      
                    }} /> 
                <div  className='interactionButtonsWithMessages'>
                    <button onClick={copyMessage} className='interactionButtonsWithMessages__item'></button>
                    <button onClick={send} className='interactionButtonsWithMessages__item'></button>
                    <button className='interactionButtonsWithMessages__item'></button>
                    <button onClick={deleteMessage} className='interactionButtonsWithMessages__item'></button>
                </div>
                </div>   
                 
    </div>
    : <SendUserList setOpen={setSendModal} />   
  )
}

export default Contextmenu