import { onValue, ref } from 'firebase/database'
import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { db } from '../firebase'
import { setOpneContextmenu } from '../store/slices/contextmenu'
import { getLocalStorage } from '../utils/localstorage'

function Messeg({messeg,myMesseg,chatId}) {
  const messagDate =  messeg.messagTime.substr(0,5)
  const dispatch = useDispatch()
  const openContextmenu = (e,messageId,text,messeg) =>{
    e.preventDefault()
    dispatch(setOpneContextmenu({
      x: e.clientX,
      y: e.clientY,
      open: true,
      messageId,
      chatId,
      text,
      messeg
    }))
  }
  const getUserInfo = (id) =>{
    let user = {}
    onValue(ref(db,`users/${id}`), Snapshot =>{
       user = Snapshot.val()
    })
    return <div className='wrapSendMessag'>
      <img className='imgSend' src="https://cdn-icons-png.flaticon.com/512/2958/2958864.png" alt="" />
      <div className='sendImgUser-wrap'><img className='sendImgUser' src={user.img} alt={user.name} /></div>  
    </div>
  } 
  return (
    <>
        {myMesseg ? 
            <div onContextMenu={(e) => openContextmenu(e,messeg.id,messeg.text,messeg)} className="messag-container-right">
                  <div  className="message message-my ">
                  {messeg.send && getUserInfo(messeg.send)}
                  <div className='message-content'>
                        <div className='message__img-wrap'>{messeg.img ? messeg.img.map((img,index) => <div key={index} className='img-wrap'><img  src={img} className='messeg__img'  alt="" /></div>) : ''}</div>
                        <p className="message-text">{messeg.text}</p>
                        <div className="message-time message-time-my">
                          {messagDate}
                          {messeg.reactions ?
                          <div className='reactions'>
                            {Object.keys(messeg.reactions).map(e => e)}
                          </div> : ''
                        }
                        </div>
                    </div>
                </div>    
            </div>
            :  
            <div onContextMenu={(e) => openContextmenu(e,messeg.id,messeg.text,messeg)} className="messag-container-left">
                  <div  className="message">
                  {messeg.send && getUserInfo(messeg.send)}
                  <div className='message-content'>
                        <div className='message__img-wrap'>{messeg.img ? messeg.img.map(img => <div className='img-wrap' key={img}><img  src={img} className='messeg__img'  alt="" /></div>) : ''}</div>
                        <p className="message-text">{messeg.text}</p>
                        <div className="message-time message-time-my">
                          {messagDate}
                          {messeg.reactions ?
                          <div className='reactions'>
                            {Object.keys(messeg.reactions).map(e => e)}
                          </div> : ''
                        }
                        </div>
                    </div>
                </div>    
            </div>
      }
    </>
  )
}

export default Messeg


